// breakpoints

$S: 480px;
$M: 600px;
$L: 1170px;

// media queries

@mixin MQ($canvas) {
	@if $canvas==S {
		@media only screen and (min-width: $S) {
			@content;
		}
	}

	@else if $canvas==M {
		@media only screen and (min-width: $M) {
			@content;
		}
	}

	@else if $canvas==L {
		@media only screen and (min-width: $L) {
			@content;
		}
	}
}



@include MQ(M) {
	// style goes here
}

//font smoothning

@mixin font-smoothing {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}